html * { padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif, "Franklin Gothic Medium",
  "Arial Narrow", Arial, sans-serif;
}


html::-webkit-scrollbar {
  width: 0rem;
}

html::-webkit-scrollbar-track {
  /* background: #67814e; */
  box-shadow: 15px 15px 25px 15px rgb(0, 0, 0, 0.5);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
}

html::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #85F9EF 0%, rgba(130, 135, 244, 0.77) 100%);


  box-shadow: 15px 15px 25px 15px rgba(226, 222, 222, 0.856);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
