/* Mobile S */
@media screen and (min-width: 320px) and (max-width: 374px) {
    /* CSS rules for Mobile S */

    .Base {

        background: linear-gradient(180deg, #85F9EF 0%, rgba(130.33, 134.89, 244.37, 0.77) 100%);
        height: 99vh;
        width: 99vw;
        display: flex;
        justify-content: center;
        align-items: center;

    }

    .title {

        height: 10rem;
        width: 30rem;
        color: aliceblue;
        /* background-color: black; */
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        row-gap: 10px;

    }

    .mytitle {

        font-size: 35px;
        background: black;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;

    }

    .para{

        color: black;
        font-weight: 600;

    }

    .Icon{

        height: 20rem;
        width: 20rem;
        /* background-color: black; */

    }

    .myIcon{

        height: 90px;

    }


}

