/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    /* CSS rules for Tablet */
    .Base {

        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
        min-height: 100vh;
        min-width: 100vw;
        /* display: flex; */

    }

    .head {
        min-width: 100%;
        display: flex;
        align-items: flex-start;
        justify-self: start;
        /* background: black; */

    }

    .brick {

        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;
        /* justify-content: space-evenly; */
        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
        min-height: 8vh;
        min-width: 25vw;
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
    }

    .maintext {
        font-size: 2rem;
    }

    .container {

        min-height: 92vh;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .containerin {

        background: linear-gradient(360deg, rgba(170, 255, 248, 0.68) 0%, rgba(53, 255, 237, 0.00) 100%);
        border: 1px solid #3a373b;
        border-radius: 1%;
        display: flex;
        align-items: center;
        row-gap: 10px;
        justify-content: space-evenly;
        min-height: 70vh;
        min-width: 80vw;

    }

    .container_text {

        height: 22rem;
        width: 20rem;
        display: flex;
        flex-direction: column;
        column-gap: 1px;
        align-items: flex-start;
        justify-content: space-evenly;
        
    }
    .container_text h1{
        font-size: 1.5rem;
        margin-left: 1.3rem;

    }

    .container_text p 
    {
        font-variant: normal;
        font: bold;
        font-size: 0.8rem;
        font-weight: bolder;
        text-align: left;
        margin-left: 1.3rem;
    }
    
    .container_img {
/*         
        background: rgb(182, 47, 47); */
        height: 25rem;
        width: 25rem;
        display:flex;
        align-items: center;
        justify-content: space-evenly;
        
        
    }
    #circle
    {
        display:flex;
        /* background: linear-gradient(360deg, rgba(170, 255, 248, 0.68) 0%, rgba(53, 255, 237, 0.00) 100%); */
        border: 20px solid hsla(214, 88%, 67%, 0.993);
        align-items: center;
        justify-content: center;
        width: 18rem;
        height: 18rem;
        border-radius: 100%;
    }
   
}

