/* Mobile S */
@media screen and (min-width: 320px) and (max-width: 374px) {
    /* CSS rules for Mobile S */
    .Base {
        display:none;
    }
}

/* Mobile M */
@media screen and (min-width: 375px) and (max-width: 424px) {
    /* CSS rules for Mobile M */
    .Base {
        display:none;
    }
}

/* Mobile L */
@media screen and (min-width: 425px) and (max-width: 767px) {
    /* CSS rules for Mobile L */
    .Base {
        display:none;
    }
}

/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    /* CSS rules for Tablet */
    /* .Base {
        display:none;
    } */

    .Base {
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
        min-height: 100vh;
        min-width: 100vw;
    }

    .head {

        min-width: 100%;
        display: flex;
        flex-direction: row-reverse;
    }

    .brick {

        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;
        /* justify-content: space-evenly; */
        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
        min-height: 8vh;
        min-width: 25vw;
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
    }

    .maintext {
        font-size: 1.2rem;
    }

    .ico {
        height: 1.9rem;
    }

    .content_store {
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        /* column-gap: 5rem; */
        min-height: 92vh;
        min-width: 100%;
    }
    .cont {
        flex-direction: column;
        background-image: linear-gradient(to bottom, #58f1ee, #58ebf2, #5de5f4, #64dff5, #6cd9f4, #6ed4f6, #72cff6, #78c9f6, #7bc3f8, #82bcf9, #8cb5f8, #97adf5);
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
        backdrop-filter: blur(20px);
        -webkit-backdrop-filter: blur(20px);
        border-radius: 10px;
        border: 1px solid rgba(255, 255, 255, 0.18);
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        min-height: 60vh;
        width: 95%;
    }

    .innsercont {
        min-height: 20rem;
        margin-bottom: 10px;
        margin-top: 10px;
        width: 60%;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
        backdrop-filter: blur(0px);
        -webkit-backdrop-filter: blur(0px);
        border-radius: 10px;
        border: 1px solid rgba(255, 255, 255, 0.18);
        transition: 1s;
        text-align: center;

    }
    .innsercont:hover{
        transform: scale(1.1);
    }

    .innser_cont_head {

        min-height: 5rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        font-size: 1.2rem;
        font-weight: 700;
    }
    
    .innser_cont_info {
        
        /* min-height: 15rem; */
        min-height: 10rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        width: 100%;


    }

    .innser_cont_info p {
        width: 100%;
        font-weight: 200;
        font-size: 1rem;
    }




}

