/* Mobile S */
@media screen and (min-width: 320px) and (max-width: 374px) {
    /* CSS rules for Mobile S */
    .Base {
        display :none
    }
}

/* Mobile M */
@media screen and (min-width: 375px) and (max-width: 424px) {
    /* CSS rules for Mobile M */
    .Base {
        display :none
    }
}

/* Mobile L */
@media screen and (min-width: 425px) and (max-width: 767px) {
    /* CSS rules for Mobile L */
    .Base {
        display :none
    }
}

/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    /* CSS rules for Tablet */
    .Base {
        min-height: 100vh;
        min-width: 100vw;
        background-image: linear-gradient(to bottom, #77fcf3, #71f4f9, #72ecfc, #77e4fe, #80dbfc, #81d5fc, #84cefc, #89c7fa, #8ac0fc, #8fb9fc, #96b1fb, #a0a9f8);
    }

    .head {

        min-width: 100%;
        display: flex;
        align-items: flex-start;
        justify-self: start;
        flex-direction: row-reverse;
    }

    .brick {

        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;
        /* justify-content: space-evenly; */
        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
        min-height: 8vh;
        min-width: 25vw;
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
    }

    .maintext {
        font-size: 1.5rem;
    }

    .content_store {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-evenly;
        width: 100vw;
        min-height: 82vh;
        /* background-color: whitesmoke; */
    }
    .setbtn
    {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100vw;
        min-height: 10vh;
        /* background-color: whitesmoke; */

    }
    .card {
        margin: 20px 0;
        width: calc(50% - 20px);
        height: 250px;
        max-width: 374.438px;
        background-color: rgb(235, 207, 207);
        transition: transform 0.6s ease;
    }
    .card:hover {
        transform: scale(1.1);
    }
    
    .card img {
        aspect-ratio: 1 / 1; 
        width: 100%;
        height: 100%;
    }
}

