/* Mobile S */
@media screen and (min-width: 320px) and (max-width: 374px) {

    /* CSS rules for Mobile S */
    .Base {

        display: none;

    }
}

/* Mobile M */
@media screen and (min-width: 375px) and (max-width: 424px) {

    /* CSS rules for Mobile M */
    .Base {

        display: none;

    }
}

/* Mobile L */
@media screen and (min-width: 425px) and (max-width: 767px) {

    /* CSS rules for Mobile L */
    .Base {

        display: none;

    }
}

/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {

    /* CSS rules for Tablet */
    /* .Base {

        display: none;

    } */

    .Base {

        background-color: #1A2431;
        min-height: 26vh;
        min-width: 100vw;
        display: flex;
        justify-content: space-evenly;

    }

    .Left {

        /* background-color: aquamarine; */
        min-height: 5rem;
        min-width: 15rem;
        padding-left: 25px;

    }

    .Logo {

        /* background-color: brown; */
        height: 3rem;
        width: 20rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;


    }

    .logotext {

        font-size: 1.5rem;
        font-weight: bold;
        color: azure;
        /* margin-left: 2rem; */


    }

    .Texts {

        /* background-color: blueviolet; */
        height: 11rem;
        width: 15rem;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        /* margin-left: 1rem */
    }

    .para {

        font-size: 0.9rem;
        text-align: left;
        color: azure;

    }



    .Mid {

        /* background-color: blueviolet; */
        min-height: 5rem;
        min-width: 10rem;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        /* padding-left: 25px; */

    }

    .spacing {

        height: 0.5rem;

    }

    .list {

        text-align: left;
        font-size: 1.2rem;
        color: azure;
        cursor: pointer;

    }

    .list:hover {

        color: cyan;

    }

    .Right {

        /* background-color: blue; */
        min-height: 5rem;
        min-width: 20rem;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

    }

    .map_store {

        background-color: azure;
        min-height: 12rem;
        min-width: 18rem;

    }



}





