/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    /* CSS rules for Tablet */
    /* .Base{

        display: none;

    } */

    .Base {
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
        min-height: 99vh;
        min-width: 99vw;
    }

    .head {

        min-width: 100%;
        display: flex;
        align-items: flex-start;
        justify-self: start;
    }

    .brick {

        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 10px;
        /* justify-content: space-evenly; */
        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
        min-height: 8vh;
        min-width: 25vw;
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
    }

    .maintext {
        font-size: 1.2rem;
    }

    .content_store {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        column-gap: 5rem;
        min-height: 92vh;
        min-width: 100%;
    }

    .container1 {

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        row-gap: 1rem;
        flex-wrap: wrap;
        /* background-color: rgb(197, 50, 50); */
        height: 39rem;
        width: 41rem;

    }

    .abt_title {
        font-size: 1.4em;
        text-decoration: underline dotted;
    }

    .paragraph p {

        display: flex;
        align-items: flex-start;
        justify-content: space-around;
        row-gap: 1rem;
        font-variant: normal;
        font: bold;
        font-size: 1rem;
        font-weight: bolder;
    }

    .explore_btn {

        height: 7vh;
        width: 25vw;
        border-radius: 15px;
        /* margin-left: 200px; */
        color: darkblue;
        font-size: 1.1rem;
        font-weight: bold;
        background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);

    }

    .explore_btn:hover {

        border: 2px solid rgba(96, 102, 251, 0.68);
        color: darkblue;
        text-transform: uppercase;

    }

    .container2 {

        display: flex;
        justify-content: center;
        align-items: center;
        /* background-color: rgb(126, 27, 27); */
        height: 34rem;
        width: 29rem;
        background: rgba(255, 255, 255, 0.19);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(0px);
        -webkit-backdrop-filter: blur(0px);
        border: 1px solid rgba(255, 255, 255, 0.14);
        margin-bottom: 10px;
        /* margin: 50px; */

    }

    .image {
        width: 75%;
        /* Adjust the width of the div */
        height: 75%;
        display: flex;
        /* Adjust the height of the div */
    }

    .image img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
        /* margin-right: 80px; */
    }

    .content2 {

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        /* background-color: blueviolet; */
        background-image: linear-gradient(to bottom, #85ccf8, #7eccf8, #76ccf7, #6eccf7, #65ccf6, #5ac8f7, #4fc4f9, #43c0fa, #3db6fd, #44abfe, #559ffd, #6992f9);
        height: 100vh;
        justify-content:space-evenly;
    }

    .video_content {

        display: flex;
        margin-top: 15px;
        align-items: center;
        justify-content:space-evenly;
        height: 50%;
        width: 40%;
        
    }
    .video_content div
    {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, 0.19);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(0px);
        -webkit-backdrop-filter: blur(0px);
        border: 1px solid rgba(255, 255, 255, 0.14);
    }
    .vidz {

        height: 90%;
        width: 90%;
        object-fit: contain;

    }
    
    .text {
        
        height: 50%;
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        flex-wrap: no-wrap;
        row-gap: 1rem;


        
        /* display: flex;
        align-items: flex-start;
        justify-content: space-around; */
        /* row-gap: 1rem; */
        font-variant: normal;
        font: bold;
        font-size: 0.9rem;
        font-weight: bolder;
        /* background-color: rgb(18, 214, 214); */
    
    }







}

