        display: none;
    }
}

/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {

    /* CSS rules for Tablet */
    .Base {

        height: 10vh;
        width: 100vw;
        background: linear-gradient(92deg, rgba(0, 8, 255, 0.52) 0%, rgba(53.12, 255, 237.45, 0.68) 100%);
        display: flex;
        flex-direction: row;

    }

    .Logo_cont {

        /* background-color: blueviolet; */
        min-height: 100%;
        min-width: 15%;
        display: flex;
        padding-left: 10px;
        justify-content: flex-start;
        align-items: center;

    }

    .logotxt {

        color: #1E1E1E;
        font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        font-size: 1.6rem;

    }

    .right_cont {

        min-height: 100%;
        min-width: 85%;
        display: flex;
        justify-content: flex-end;
        column-gap: 50px;
        align-items: center;
        padding-right: 30px;

    }

    .HY {

        font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
        font-size: 1.1rem;
        font-weight: bold;
        color: #1E1E1E;
        cursor: pointer;
        position: relative;
        text-decoration: none;

    }

    .HY::after{

        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        bottom: 0;
        left: 0;
        background-color: #7B81FF;
        transform-origin: bottom right;
        transition: transform 0.25s ease-out;

    }

    .HY::after{
        
        transform: scaleX(0);
      }

    .HY:hover:hover::after{

        transform: scaleX(1);

    }

    
}

