/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {

    /* CSS rules for Tablet */
    .Base {

        /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%); */
        min-height: 90vh;
        min-width: 100vw;
        background-image: url('../../Icons/iPad\ mini\ 8.3\ -\ 1.svg  ');
        background-size: 100% 100%;
        background-size: cover;
        display: flex;
        row-gap: 10px;
        justify-content: space-evenly;

        /* display: flex; */

    }

    .text_left {
        height: 88vh;
        width: 52vw;
        align-items: center;
        justify-content: space-around;

        /* background-color: blue; */
    }

    .maintext_1 {
        font-size: 2.3rem;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: bold;
        color: rgb(2, 3, 31);
        margin-top: 6rem;
        
    }

    .maintext_1 .spann {


        position: relative;
        color: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
        -webkit-text-stroke: 0.3vw #fff;
        text-transform: uppercase;

    }

    .maintext_1 .spann::before{


        content: attr(data-text);
        position: absolute;
        top: 0;
        left: 0;
        color: #7B81FF;
        -webkit-text-stroke: 0vw #000AFF;
        border-right: 3px solid #7B81FF;
        overflow: hidden;
        animation: animate 6s linear infinite;


    }

    @keyframes animate{

        0%,10%,100%
        {
            width: 0;
        }
        70%,90%
        {

            width: 94%;

        }

    }

    .maintext_2 {
        font-size: 2.3rem;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: bold;
        color: rgb(2, 3, 31);


    }

    .maintext_1 span {

        color: hsla(0, 0%, 100%, 0.884);

    }

    .maintext_3 {
        margin-top: 2rem;
    }

    .button72 {
        align-items: center;
        background-color: initial;
        border-width: 0;
        box-shadow: rgba(57, 31, 91, 0.24) 0 2px 2px, rgba(179, 132, 201, 0.4) 0 8px 12px;
        color: rgb(2, 3, 31);
        cursor: pointer;
        font-family: Quicksand, sans-serif;
        font-size: 1rem;
        font-weight: 700;
        height: 4rem;
        justify-content: center;
        letter-spacing: .04em;
        line-height: 16px;
        margin-top: 6rem;
        padding: 18px 18px;
        text-align: center;
        text-decoration: none;
        text-shadow: rgba(255, 255, 255, 0.4) 0 0 4px, rgba(255, 255, 255, 0.2) 0 0 12px, rgba(57, 31, 91, 0.6) 1px 1px 4px, rgba(57, 31, 91, 0.32) 4px 4px 16px;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        vertical-align: baseline;
        border-radius: 75px;
        border: 3px solid #7B81FF;
        cursor: pointer;
    }

    .button72:hover {
        background: linear-gradient(180deg, rgba(81, 88, 255, 0.43) 0%, rgba(89, 251, 237, 0.68) 100%);
        /* background-image: linear-gradient(#B384C9, #391F5B 50%); */
    }



    .eclips_img {
        height: 70vh;
        width: 40vw;
        background-image: url('../../Icons/Ellipse\ 11\ \(1\).svg  ');
        background-size: 100% 100%;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }

    .Bottle_img {
        display: flex;
        align-items: center;
        justify-content: space-evenly;



    }

    .Bottle_img img {

        height: 10rem;
        width: 12rem;
        margin-right: 1.9rem;
    }

}

