/* Tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {
  /* CSS rules for Tablet */
  /* .Base{

    display: none;

  } */

  .Base {
    background: linear-gradient(180deg, #00f4e0a1 0%, #7b00ffaf 100%);
    /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
    min-height: 100vh;
    min-width: 100vw;
  }

  .head {

    min-width: 100%;
    display: flex;
    flex-direction: row-reverse;
    /* align-items: flex-end;
    justify-content: flex-end; */
  }

  .brick {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 10px;
    /* justify-content: space-evenly; */
    /* background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgb(250, 250, 250) 100%); */
    min-height: 8vh;
    min-width: 28vw;
    background: linear-gradient(180deg, rgba(53, 255, 237, 0.68) 0%, rgba(96, 102, 251, 0.68) 100%);
  }

  .title {
    font-size: 1.2rem;
  }
  .main_container {

    /* background-color: #9b9eea; */
    min-height: 92vh;
    min-width: 90vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }

  .container_left {
    /* background-color: chartreuse; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 2rem;
    align-items: flex-start;
    height: 41rem;
    width: 35rem;
    margin-right: 50px;
    margin-left: 15px;

  }

  .MT {
    width: 100%;
    display: flex;
    justify-content: center;
    /* background-color: #703af8; */
  }

  .title2 {
    font-size: 2rem;
    margin-top: 15%;
    text-align: left;
    text-decoration: underline;
    /* color: #1e1e1e; */
  }

  .para_cont {

    /* background-color: #0f0e0e; */
    min-height: 35vh;
    min-width: 20vw;
    display: flex;
    justify-content: flex-end;
    text-align: center;

  }

  .para {
    font-size: 1rem;
    font-weight: bold;
    word-wrap: normal;
    color: #1e1e1e;
  }

  .social {

    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: aqua; */
    height: 100%;
    width: 100%;

  }

  .forIcons {
    /* background-color: aqua; */
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: 80px;
    width: 50vw;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(0px);
    border-radius: 28px;

  }

  .firstIc {
    height: 5vh;
  }

  .container_right {
    /* background-color: chocolate; */
    height: 40rem;
    width: 30rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    margin-bottom: 25px;
  }

  .book {
    height: 70vh;
    width: 60vw;
    display: flex;
    justify-content: center;
    border-radius: 30px;
  }

  .book form {
    min-height: 20rem;
    min-width: 0%;
    display: flex;
    justify-content: center;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    flex-wrap: wrap;
  }

  .book form h1 {
    /* margin-top: 30px; */
    width: 30rem;
    text-align: center;
  }

  .book form input {
    width: 11rem;
    height: 2rem;
    /* padding-left: 10px; */
    outline: none;
    border: none;
    font-size: 15px;
    background: none;
    border-bottom: 2px solid #703af8;
  }

  .book form input::placeholder {
    color: #0f0e0e;
    font-weight: 600;
  }

  .book form #lastName,
  .book form #mobile {
    margin-left: 1rem;
  }

  .book form textarea {
    background: none;
    border: none;
    border-bottom: 2px solid #703af8;
    color: #0f0e0e;
    font-weight: 200;
    font-size: 14px;
    padding: 10px;
    outline: none;
    min-width: 24rem;
    min-height: 8rem;
  }

  textarea::-webkit-scrollbar {
    width: 1em;
  }

  textarea::-webkit-scrollbar-thumb {
    background-color: rgba(131, 4, 250, 0.713);
  }

  .book form #button {
    border: 2px solid #7a24fb;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(0px);
    border-radius: 28px;
    font-weight: 600;
    font-size: 20px;
    color: #000000;
    min-width: 10rem;
    padding: 0;
    transition: 0.3s;
    min-height: 2.2rem;
    text-align: center;
    cursor: pointer;

  }

  .book form #button:hover {
    opacity: 0.7;
    border: 2px solid white;
    box-shadow: 2px 5px #0b0212;
  }

  .five {
    width: 100%;
  }

  textarea::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    /* Chrome/Opera/Safari */
    position: absolute;
    top: 15%;
    left: 29%;
    transform: translate(-50%, -50%);
    color: rgb(0, 0, 0);
    font-weight: bold;

  }





}


